import Zmage from './zmage/index.jsx';
import Swiper from './swiper/index.jsx';

class Tools extends React.Component {
  render() {
    return (
      <Row className="tools">
        <Col xs={ 24 } sm={ 24 } md={ 24 } lg={ 12 }>
          <Zmage />
        </Col>
        <Col xs={ 24 } sm={ 24 } md={ 24 } lg={ 12 }>
          <Lrzpic />
        </Col>
        <Col span={ 24 }>
          <Swiper />
        </Col>
      </Row>
    );
  }
}

export default Tools;
