/*  */
import './index.less';
import React from 'react';
import { Row, Col, Empty, Tooltip, Tag, Divider, Upload, Icon, Modal, message, Card, Alert } from 'antd';
import lrz from 'lrz/dist/lrz.bundle.js'; // 
import ReactJson from 'react-json-view';

class Lrzpic extends React.Component {
  compressImg = (e) => {
    lrz(e).then(res => {
      this.setState({
        lrzfile: {...res,
          file: {
            size: res.file.size,
            type: res.file.type
          }
        }
      })
    }).catch(() => {
      message.warning('');
    });
    return false;
  };
  getBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = error => reject(error);
    });
  };
  handleCancel = () => this.setState({ previewVisible: false });
  handlePreview = async file => {
    if (!file.url && !file.preview) {
      file.preview = await this.getBase64(file.originFileObj);
    }
    this.setState({
      previewImage: file.url || file.preview,
      previewVisible: true,
    });
  };
  handleChange = ({ fileList }) => {
    if (window.$globalmethod.isEmpty(fileList)) {
      this.setState({
        lrzfile: null,
        fileList: []
      })
    } else {
      this.setState({ fileList })
    }
  };
  constructor(props) {
    super(props);
    this.state = {
      previewVisible: false,
      previewImage: '',
      fileList: [],
      lrzfile: null
    };
  };
  render() {
    const { previewVisible, previewImage, fileList, lrzfile } = this.state;
    const uploadButton = (
      <div>
        <Icon type="plus" />
        <div className="ant-upload-text">Upload</div>
      </div>
    );
    return (
      <Row className="lrzpic" gutter={16}>
        <Col xs={ 24 } sm={ 24 } md={ 24 } lg={ 24 }>
          <Divider orientation="left">
            <Tooltip placement="topLeft" title=''>
              <Tag color="blue" onClick={ () => window.open('https://github.com/think2011/localResizeIMG', '_blank') }></Tag>
            </Tooltip>
          </Divider>
          <Upload action="/"
            fileList={fileList}
            listType="picture-card"
            onPreview={this.handlePreview}
            onChange={this.handleChange}
            beforeUpload={(e)=>this.compressImg(e)}>
            {fileList.length >= 1 ? null : uploadButton}
          </Upload>
          <Alert message="resulesizeb" type="info" banner />
          <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
            <img alt="example" style={{ width: '100%' }} src={previewImage} />
          </Modal>
        </Col>
        <Col xs={ 24 } sm={ 24 } md={ 24 } lg={ 12 }>
          <Card title="">
          {
            window.$globalmethod.isEmpty(fileList[0]) ?
            <Empty description="" /> :
            <ReactJson src={fileList[0]} />
          }
          </Card>
        </Col>
        <Col xs={ 24 } sm={ 24 } md={ 24 } lg={ 12 }>
          <Card title="">
          {
            window.$globalmethod.isEmpty(lrzfile) ?
            <Empty description="" /> :
            <ReactJson src={lrzfile} />
          }
          </Card>
        </Col>
      </Row>
    );
  }
}

export default Lrzpic;
